


























































































































export default {
	data() {
		return {
			// stepName : '名称查重',
			// stepName : '注册登记',
			// stepName : '选择套餐',
			// stepName : '查看进度',
			// stepName : '更多服务',
			stepName : '选择行业',
			currentStep : 0, // 默认0 为选择行业
		};
	},
	onLoad(e) {
		var self = this;
		uni.getStorage({
			key: 'currentStep',
			success: function (res) {
				console.log('storage',res.data);
				// self.currentStep = 1;
			}
		});
		console.log('主页打印onload的e',e);
		switch (e.from){
			case 'choiceIndustryOne':
				this.stepName = '名称查重';
				this.currentStep = 1;
				uni.setStorage({
					key: 'currentStep',
					data: '1',
					success: function () {
						console.log('success');
					}
				});
				break;
			case 'choiceInvitationCode':
				this.stepName = '名称查重';
				this.currentStep = 1;
				uni.setStorage({
					key: 'currentStep',
					data: '1',
					success: function () {
						console.log('success');
					}
				});
				break;
			case 'name_repeat':
				this.stepName = '注册登记';
				this.currentStep = 2;
				uni.setStorage({
					key: 'currentStep',
					data: '2',
					success: function () {
						console.log('success');
					}
				});
				break;
			case 'registrationInfo':
				this.stepName = '选择套餐';
				this.currentStep = 3;
				uni.setStorage({
					key: 'currentStep',
					data: '3',
					success: function () {
						console.log('success');
					}
				});
				break;
			case 'waitPay':
				this.stepName = '查看进度';
				this.currentStep = 4;
				uni.setStorage({
					key: 'currentStep',
					data: '4',
					success: function () {
						console.log('success');
					}
				});
				break;
			case 'review':
				this.stepName = '查看进度';
				this.currentStep = 4;
				uni.setStorage({
					key: 'currentStep',
					data: '4',
					success: function () {
						console.log('success');
					}
				});
				break;
			default:
				break;
		}
	},
	methods: {
		clickStep() {
			switch (this.stepName){
				case '名称查重':
					uni.redirectTo({
						url: '../name_repeat/name_repeat'
					});
					break;
				case '注册登记':
					uni.redirectTo({
						url: '../registration/registration'
					});
					break;
				case '选择套餐':
					uni.redirectTo({
						url: '../waitPay/waitPay'
					});
					break;
				case '查看进度':
					uni.redirectTo({
						url: '../review/review'
					});
					break;
				case '更多服务':
					uni.redirectTo({
						url: '../choiceIndustry/choiceIndustry'
					});
					break;
				case '选择行业':
					uni.redirectTo({
						url: '../choiceIndustry/choiceIndustry'
					});
					break;
					
				default:
					break;
			}
		},
		reApply() {
			uni.showModal({
			title: '提示',
			content: '重新申请将会清空完成的所有步骤',
			success: function (res) {
				if (res.confirm) {
					console.log('用户点击确定');
				} else if (res.cancel) {
					console.log('用户点击取消');
				}
			}
		});
		}
	}
}
